export default {
	name: 'skeleton',
	props: {
		showSke: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			base: 'skeleton',
			sysInfo: {},
			rectLists: [],
			circleLists: []
		};
	},
	methods: {
		init() {
			this.getSystemInfo()
			this.drawFn()
		},
		getSystemInfo() {
			const sysInfo = uni.getSystemInfoSync()
			this.sysInfo = {
				width: sysInfo.windowWidth,
				height: sysInfo.windowHeight
			}
		},
		drawFn() { // 绘制
			this.dynamicFn('rect', res => {
				this.rectLists = res[0]
			})
			this.dynamicFn('radius', res => {
				this.circleLists = res[0]
			})
		},
		dynamicFn(dom, done) {
			uni.createSelectorQuery()
				.selectAll(`.${this.base}-${dom}`)
				.boundingClientRect()
				.exec(res => done(res))
		},
		stopScroll(){
			// 禁止页面滚动
		}
	}
}
